}

.medical-team-wrap {
    text-align: center;
}

.medical-team-wrap img {
    max-width: 100%;
    border-radius: 20px;
}

.medical-team-wrap h4 {
    margin-top: 32px;
    font-size: 18px;
    font-weight: 600;
    color: #404040;
}

.medical-team-wrap h5 {
    margin-top: 12px;
    font-size: 14px;
    font-weight: 600;
    color: #828282;
}


/*--------------------------------------------------------------
# Services
--------------------------------------------------------------*/
.quality-services {
    padding: 100px 0;
}

.quality-services h2 {
    text-align: center;
}

.quality-services p {
    font-size: 14px;
    line-height: 2;
    color: #828282;
}

.services-tabs {
    margin: 100px 0;
}

/*--------------------------------------------------------------
# News
--------------------------------------------------------------*/
.blog-page .the-news {
    padding-bottom: 20px;
}

.blog-page .the-news-wrap {
    margin-bottom: 80px;
}

/*--------------------------------------------------------------
# Pagination
--------------------------------------------------------------*/
@media screen and (min-width: 992px){
    .pagination {
        margin-bottom: 100px;
    }
}

.pagination a {
    margin-right: 16px;
    font-size: 16px;
    font-weight: 600;
    line-height: 1;
    color: #828282;
    text-decoration: none;
}

.pagination a:hover,
.pagination .active a {
    color: #18a3eb;
}

/*--------------------------------------------------------------
# Sidebar
--------------------------------------------------------------*/
.sidebar {
    padding: 100px 0;
}

/*
# Search
--------------------------------*/
.search-widget input[type="search"] {
    width: calc(100% - 90px);
    padding: 8px 30px 8px 20px;
    border: 0;
    border-radius: 30px;
    font-size: 14px;
    outline: none;
    background: #f0f4f8;
    color: #404040;
}

.search-widget button[type="submit"] {
    padding: 10px 32px 9px;
    border: 0;
    margin-left: -32px;
    border-radius: 30px;
    font-size: 12px;
    font-weight: 500;
    font-style: italic;
    color: #fff;
    cursor: pointer;
    outline: none; /* ff3.6+ */ /* safari4+,chrome */ /* safari5.1+,chrome10+ */ /* opera 11.10+ */ /* ie10+ */
    background: linear-gradient(270deg, rgba(50,200,250,1) 0%, rgba(88,125,228,1) 100%); /* w3c */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#18a3eb', endColorstr='#587de4',GradientType=1 ); /* ie6-9 */
}

.widget-title {
    margin-top: 52px;
    margin-bottom: 32px;
    font-size: 24px;
    font-weight: 600;
    color: #404040;
}

/*
# Categories
--------------------------------*/
.sidebar-cats ul {
    list-style: none;
}

.sidebar-cats li {
    margin-bottom: 15px;
    font-size: 14px;
}

.sidebar-cats li a {
    color: #828282;
}

/*
# Popular Posts
--------------------------------*/
.popular-posts ul {
    list-style: none;
}

.popular-posts li {
    margin-bottom: 32px;
}

.popular-posts li:nth-last-of-type(1) {
    margin-bottom: 0;
}

.popular-posts figure,
.popular-posts figure a ,
.popular-posts figure img {
    display: block;
    margin: 0;
}

.popular-posts figure {
    width: 58px;
}

.popular-posts .entry-content {
    width: calc(100% - 72px);
}

.popular-posts h3 {
    font-size: 14px;
}

.popular-posts h3 a {
    color: #828282;
}

.popular-posts .posted-date {
    font-size: 12px;
    font-weight: 500;
    color: #18a3eb;
}

/*
#Opening hours
--------------------------------*/
.sidebar .opening-hours,
.sidebar .emergency-box {
    margin-top: 80px;
}

.sidebar .emergency-box .button {
    max-width: 220px;
    padding: 6px 10px 4px;
    margin: 32px auto;
}

/*--------------------------------------------------------------
# Contact Page
--------------------------------------------------------------*/
.contact-page-short-boxes {
    padding: 100px 0;
}

.contact-info ul {
    list-style: none;
    font-size: 14px;
    font-weight: 500;
    color: #828282;
}

.contact-info ul li {
    margin-top: 20px;
    margin-bottom: 12px;
    font-weight: 500;
}

.contact-info ul li span {
    display: inline-block;
    color: #404040;
}

.contact-info ul li span {
    width:64px;
}

.contact-page-short-boxes .opening-hours ul li {
    margin-top: 20px;
}

.contact-page-short-boxes .emergency-box {
    padding: 32px;
}

.contact-page-short-boxes .emergency-box .button {
    max-width: 220px;
    padding: 6px 10px 4px;
    margin: 0 auto 24px;
}

.contact-form {
    margin-bottom: 100px;
}

.contact-form h2 {
    margin-bottom: 24px;
    background: #fff;
    font-size: 24px;
    font-weight: 600;
    color: #404040;
}

.contact-form input[type="text"],
.contact-form input[type="email"],
.contact-form textarea {
    width: 100%;
    padding: 15px 20px;
    margin-top: 30px;
    border: 0;
    background: #f0f4f8;
    outline: none;
    font-size: 12px;
    line-height: 1;
    font-style: italic;
    color: #828282;
}

.contact-form input[type="submit"] {
    font-size: 16px;
    font-weight: 500;
    margin-top: 30px;
    cursor: pointer;
}

.contact-page-map {
    margin-bottom: 100px;
}

.contact-page-map iframe {
    display: block;
    width: 100%;
    height: 560px;
}

/*--------------------------------------------------------------
# Footer
--------------------------------------------------------------*/
.site-footer {
    position: relative;
    padding: 48px 0;
    background: url(/preauthform/static/media/footer-bg.323b1514.png) no-repeat center #fff;
    background-size: cover;
    font-size: 14px;
    color: #828282;
}

.footer-widgets h2 {
    display: flex;
    align-items: center;
    min-height: 50px;
    margin-bottom: 24px;
    font-size: 18px;
    font-weight: 600;
    color: #404040;
}

.foot-about p {
    font-size: 14px;
    line-height: 2;
}

.foot-about p.copyright {
    margin-top: 24px;
    font-size: 12px;
    font-weight: 500;
}

.foot-contact ul {
    list-style: none;
}

.foot-contact ul li {
    margin-bottom: 5px;
    font-weight: 500;
}

.foot-contact ul li span {
    display: inline-block;
    color: #404040;
}

.foot-contact ul li span {
    width:64px;
}

.foot-links ul {
    list-style: none;
    -webkit-column-count: 2;
            column-count: 2;
    grid-column-gap: 10px;
    -webkit-column-gap: 10px;
            column-gap: 10px;
}

.foot-links ul li {
    margin-bottom: 5px;
}

.foot-links ul li a {
    color: #828282;
}

